
.bind-con {
  background-color: #fff;
  margin-top: 0.26667rem;
}
.bind-con .mu-text-field-content {
    width: 10rem;
    font-size: 0.4rem;
    padding-left: 1.27rem;
    padding-top: 0.66667rem;
}
.bind-con .mu-text-field-content .mu-text-field-input {
      height: 0.8rem;
}
