    margin-left: 1.27rem;
    font-size: 0.4rem;
    width: 100%;
}
.bind-con .mu-text-field-focus-line {
    width: 6rem;
    margin-left: 1.27rem;
}
.bind-con .mu-text-field-label {
    width: 100%;
    color: #000;
    font-size: 0.53333rem;
    color: #1f80eb;
}
.bind-con .mu-text-field-label.float {
      font-size: 0.4rem;
      color: #000;
}



